.country_details_header {
    gap: 20px;
    align-items: center;
}

.country_details_name {
    margin-bottom: 0;
}

.country_details_header img {
    width: 70px;
    height: 40px;
    object-fit: cover;
}

.country_details_download_btn {
    all: unset;
    cursor: pointer;
    transition: .2s;
    font-size: 16px;
}

.indicatorName {
    width: 366px;
}

.countryDetailsTable {
    font-size: 14px !important;
}